  componentDidMount() {
    const processDefinitionKey = this.props.match.params.processKey;
    this.setState({submitFormFields: JSON.parse(submitForm)});
    this.setState({processKey: processDefinitionKey})
    this.handleFormSubmit = this.handleFormSubmit.bind(this);
      const domainName = JSON.parse(localStorage.getItem("domain_data")).domainName;

    const process_data = {
      method: 'POST',
      headers: {
          "X-Tenant": domainName,
          'Content-Type': 'application/json',
          "Authorization": "Bearer " + localStorage.getItem("Authorization")
          },
      body: JSON.stringify({processKey:processDefinitionKey, domainName: domainName })
    };
    fetch(PROCESS_SERVICE+"/api/process/createNewInstance", process_data)
      .then(response => {
        return new Promise((resolve, reject) => {
          response.text().then(body => {
            resolve(
              body.length ? JSON.parse(body) : null
            );
          }).catch(err => {
            reject(err);
          });
        });
      })
      .then(data => {
          if(data.length){
              this.setState({formId: data[0].id})
          }

//      console.log(data[0].validationConstraints[0].name)
      var tab = [];
      var codesTab = [];

      data.map((formField, index) =>{
          codesTab.push({formCode: formField.id})
        if(formField.validationConstraints[0] === undefined){
                tab.push({id: formField.id, label: formField.label, constraint: null})

        }else{
                tab.push({id: formField.id, label: formField.label, constraint: formField.validationConstraints[0].name})
}
      })
      this.setState({formCodes: codesTab})
      this.setState({formFields : tab});
        if(this.state.formCodes[0] !== undefined ){
         // this.setState({formKey: data.startFormKey});
          this.getFormsByFormCodes(codesTab);
          this.setState({visible:false})
        }else{
            this.props.history.push("/dashboard");

        }
      }).catch(error => {
      console.log(error);
    });

  }

