 handleFormSubmit (event ) {
    // event.preventDefault();
    const formKey = this.state.formKey;
    var data = this.state.formsData;
    const domainName = JSON.parse(localStorage.getItem("domain_data")).domainName;
    const domainId = JSON.parse(localStorage.getItem("domain_data")).domainId;
    const username = localStorage.getItem("username");
    const form_data = {
      method: 'POST',
      headers: {
          "X-Tenant": domainName,
          'Content-Type': 'application/json',
        "Authorization": "Bearer "+localStorage.getItem('Authorization')},
      body: JSON.stringify({ username: username,domainId: domainId, domainName: domainName, "startEvent": data, formCodes: this.state.formCodes, processDefinitionKey : this.state.processKey})
    };
     fetch(PROCESS_SERVICE+'/api/process/startProcess', form_data)
      .then(response => {
        return new Promise((resolve, reject) => {
          response.text().then(body => {
            resolve(
              body.length ? JSON.parse(body) : null
            );
          }).catch(err => {
            reject(err);
          });
        });
      }).then(data => {
            this.setState({processInstanceId: data.processInstanceId})
           this.props.history.push("/process/instances/"+localStorage.getItem("processName"));
      // this.props.history.push("/admin/formView");
    })
      .catch(error => {
        console.log(error);
      });

  }

