import React, {useEffect} from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Checkbox from "@material-ui/core/Checkbox";



import {useParams} from "react-router-dom";
import {CButton, CCard, CCardBody, CCardHeader, CCol, CRow} from "@coreui/react";
import {Assignment} from "@material-ui/icons";
import {PROCESS_SERVICE, SERVER_URL} from "../../constants";

export default function UserNotification(props) {
  const [checked, setChecked] = React.useState([]);
  const [userTask, setUserTask] = React.useState({});
  const [taskId, setTaskId] = React.useState("");
  const [processDefinitionKey, setProcessDefinitionKey] = React.useState("");
  const [processInstanceId, setProcessInstanceId] = React.useState("");
  const [workflowMasterName, setWorkflowMasterName] = React.useState("");
  const [taskName, setTaskName] = React.useState("");
  const [textField, setTextField] = React.useState("");
    const [domainName, setDomainName] = React.useState(JSON.parse(localStorage.getItem('domain_data')).domainName);

    const params = useParams();

  useEffect(() => {

    const taskId = props.match.params.taskId;
    setTaskId(taskId)
    const processDefinitionKey = props.match.params.processDefinitionKey;
    const workflowMasterName = props.match.params.workflowMasterName;
    const textFields = props.match.params.textFields;
    const taskName = props.match.params.taskName;
    setWorkflowMasterName(workflowMasterName)
    if( textFields !== "null"){
      setTextField(textFields)
    }
    setTaskName(taskName)
    setProcessDefinitionKey(processDefinitionKey);
    //    const domainName = JSON.parse(localStorage.getItem("domain_data")).domainName;
    //console.log(domainName);
      const get_header = {
          method: 'GET',
          headers: {
              "X-Tenant": domainName,
              'Content-Type': 'application/json'
          }
      };
    fetch(PROCESS_SERVICE+'/api/process/auth/getTaskById/' + taskId, get_header)
      .then(response => {
        return new Promise((resolve, reject) => {
          response.text().then(body => {
            resolve(
              body.length ? JSON.parse(body) : null
            );
          }).catch(err => {
            reject(err);
          });
        });
      })
      .then(data => {
        //   console.log(data.group_tasks);

        console.log(data)
        if(data != null){
          setUserTask(data);
        }
      })
      .catch(error => {
        console.log(error);
      });
  });
  const handleToggle = value => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
  };


  const completeTask = event =>{
    // event.preventDefault();
    const processInstanceId = params.processInstanceId;
    const username = localStorage.getItem("username");
    props.history.push("/execute/"+processDefinitionKey+"/"+ processInstanceId + "/" +taskId+"/" + username);

  }
  return (

        <CCard>
          <CCardHeader color="rose" icon>
            <h4 >Notification d'utilisateur</h4>
          </CCardHeader>
          <CCardBody>
            <CRow><CCol xs="10">
              <div style={{fontSize:"16px"}} className="font-weight-bold">{workflowMasterName}</div>
              {/*<small className="text-muted float-right mt-1">Just now</small>*/}
              <div>
                <div className="text-muted font-weight-bold">{taskName }</div>

                <div className="text-muted">{textField }</div>

              </div>

            </CCol>
            <CCol xs="2">
              <CButton color="info" className="float-right" onClick={event => completeTask(event)} >
                Complter
              </CButton>
            </CCol></CRow>

          </CCardBody>
        </CCard>

  );
}
