 getFormsByFormCodes (data) {

    const form_data = {
      method: 'POST',
      headers: {
          "X-Tenant": this.state.domainName,
          'Content-Type': 'application/json',
        "Authorization": "Bearer " + localStorage.getItem("Authorization")},
      body: JSON.stringify({formCodes : data})
    };

    fetch(FORM_SERVICE+'/api/form/getByFormCodes',form_data)
      .then(response => {
        return new Promise((resolve, reject) => {
          response.text().then(body => {
            resolve(
              body.length ? JSON.parse(body) : null
            );
          }).catch(err => {
            reject(err);
          });
        });
      })
      .then(dataVal => {

                  var table = []
                  const forms_data =JSON.parse(dataVal.forms_json);
                  var tab = [];
                  var list = [];
                 // tab.push(...forms_data[1].form.components)
                //  setFormCode(forms_data[1].formCode)
                  forms_data.map((formData, index) =>{
                  list.push(formData.components)
                   tab.push(formData.components)
                  })
                  this.setState({componentsData: tab})
//                  setForms(forms_data);
                 this.setState({forms: forms_data})

        //console.log(dataVal)
//        this.setState({formName: JSON.parse(datas.form_data).name})
//        this.setState({components: JSON.parse(datas.form_json).components});
//        this.setState({form: JSON.parse(datas.form_json)});
//        const form = JSON.parse(dataVal.form_json);

      })
      .catch(error => {
        console.log(error);
      });
  }

