  handleFormSubmit (event) {
    // event.preventDefault();
   // const formKey = this.state.formKey;
    var data = event.data;


      const domainName = JSON.parse(localStorage.getItem("domain_data")).domainName;
    const username = localStorage.getItem("username");
    const ids = this.state.editabeFormsIds;
      var formDataValues = this.state.formsData.filter( data_val =>{
          return data_val !== undefined
      })
    //  console.log(formDataValues)
    const form_data = {
      method: 'POST',
      headers: {
          "X-Tenant": this.state.domainName,
          'Content-Type': 'application/json',
        "Authorization": "Bearer " + localStorage.getItem('Authorization')},
      body: JSON.stringify({formCodes: ids, taskId: this.state.taskId, username: username, domainId: this.state.domainId, domainName: domainName, [this.state.taskId]: formDataValues, processDefinitionKey : this.state.processKey, processInstanceId: this.state.processInstanceId})
    };
   // console.log(JSON.stringify({data: data, formKey: formKey}))
    fetch(PROCESS_SERVICE+'/api/process/executeProcess', form_data)
      .then(response => {
        return new Promise((resolve, reject) => {
          response.text().then(body => {
            resolve(
              body.length ? JSON.parse(body) : null
            );
          }).catch(err => {
            reject(err);
          });
        });
      }).then(data => {
      if(data.is_complete === true){
        //alert (data.is_complete)
        // alert("the end of the process is reached !");
        this.showSuccess()

        this.props.history.push("/dashboard");
        //   this.getFormByFormCode(data.taskFormKey);
      }
      //else{
        // alert ('the process is completed')
    //    this.props.history.push("/admin/instances/"+localStorage.getItem("processName"));
    //  }
      // this.props.history.push("/admin/formView");
    })
      .catch(error => {
        console.log(error);
      });

  }
