import React, {useEffect} from "react";
import {SERVER_URL, MENU_SERVICE, MASTER_SERVICE} from "../../../../constants";

const AllMenu = () => {
    const [menu, setMenu] = React.useState([]);
    const [domainName, setDomainName] = React.useState(JSON.parse(localStorage.getItem('domain_data')).domainName);

    useEffect(()=>{
        const menu_data = {
            method: 'GET',
            headers: {
                "X-Tenant": domainName,
                'Content-Type': 'application/json',
                "Authorization": "Bearer " + localStorage.getItem("Authorization")
            }
        };
        fetch(MENU_SERVICE + '/api/menu/auth/allMenu', menu_data)
            .then(response => {
                    return new Promise((resolve, reject) => {
                        response.text().then(body => {
                            resolve(
                                body.length ? JSON.parse(body) : null
                            );
                        }).catch(err => {
                            reject(err);
                        });
                    });
                }
                //     console.log(response.json())
                //  console.log(response)
            )
            .then(data => {
                // alert(data)
                console.log(JSON.parse(data.menu))
                setMenu(JSON.parse(data.menu));
                // setMenu(data)
            })
    },[])
 return (<div>{menu}</div>)
}
export default AllMenu;