import React, {useEffect} from 'react'
import { useHistory } from "react-router";

import {
  CBadge,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CImg
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import socketIOClient from "socket.io-client";
import {SERVER_URL, USER_SERVICE} from "../constants";
import UserImage from "../views/Administration/users/img/user.png";


const TheHeaderDropdown = (props) => {
  const history = useHistory();
const [username,setUsername]=React.useState("");
const [domainName,setDomainName]=React.useState("");
const [userImage,setUserImage]=React.useState(localStorage.getItem("image"));
  useEffect(()=> {
    var img = null
    if (localStorage.getItem('domain_data') === null) {
      localStorage.clear();
      history.push("/domain");
    } else {
      setUsername(localStorage.getItem("username"))
      setDomainName(JSON.parse(localStorage.getItem("domain_data")).domainName)
      const domain = JSON.parse(localStorage.getItem("domain_data")).domainName
      const header_data = {
        method: 'GET',
        headers: {
          "X-Tenant": domain,
          "Content-Type": "application/json",
          "Authorization": "Bearer " + localStorage.getItem("Authorization")},
      };
      fetch(USER_SERVICE + "/api/auth/user/getUserByUsername/"+localStorage.getItem("username"),header_data)
          .then((response) => {
            return response.json();
          })
          .then(data => {

            const user = JSON.parse(data.user);

            if (data.photo) {
              img = "data:image/png;base64," + data.photo;
            } else {
              img = 'img/user.png';
            }
            setUserImage(img)
          })
    }
  },[userImage])

  const handleLogout = () => {
    localStorage.clear();
    global.localStorage.clear();
    history.push("/domain");
  }
  return (
    <CDropdown
      inNav
      className="c-header-nav-items mx-2"
      direction="down"
    >
      <CDropdownToggle className="c-header-nav-link" caret={false}>
        <div className="c-avatar">
          <CImg
            src={userImage}
            className="c-avatar-img"
            alt="img"
          />
        </div>
      </CDropdownToggle>
      <CDropdownMenu className="pt-0" placement="bottom-end">
        <CDropdownItem
          header
          tag="div"
          color="light"
          className="text-center"
        >
          <strong>Account</strong>
        </CDropdownItem>
        <CDropdownItem>
          <CIcon name="cil-home" className="mfe-2" />
          {domainName}
        </CDropdownItem>
        <CDropdownItem>
          <CIcon name="cil-user" className="mfe-2" />
          {username}
        </CDropdownItem>
       {/* <CDropdownItem>
          <CIcon name="cil-envelope-open" className="mfe-2" />
          Messages
          <CBadge color="success" className="mfs-auto">42</CBadge>
        </CDropdownItem>
        <CDropdownItem>
          <CIcon name="cil-task" className="mfe-2" />
          Tasks
          <CBadge color="danger" className="mfs-auto">42</CBadge>
        </CDropdownItem>
        <CDropdownItem>
          <CIcon name="cil-comment-square" className="mfe-2" />
          Comments
          <CBadge color="warning" className="mfs-auto">42</CBadge>
        </CDropdownItem>*/}
        <CDropdownItem
          header
          tag="div"
          color="light"
          className="text-center"
        >
          <strong>Settings</strong>
        </CDropdownItem>
        <CDropdownItem href={'#/user/preview/'+username} >
          <CIcon name="cil-user" className="mfe-2" />Profile
        </CDropdownItem>
        <CDropdownItem  href={'#/user/change/pwd'} >
          <CIcon name="cil-settings" className="mfe-2" />
          Changer le mot de passe
        </CDropdownItem>
        {/*<CDropdownItem>
          <CIcon name="cil-settings" className="mfe-2" />
          Paramtrage
        </CDropdownItem>
        <CDropdownItem>
          <CIcon name="cil-credit-card" className="mfe-2" />
          Paiement
          <CBadge color="secondary" className="mfs-auto">42</CBadge>
        </CDropdownItem>*/}
       {/* <CDropdownItem>
          <CIcon name="cil-file" className="mfe-2" />
          Projects
          <CBadge color="primary" className="mfs-auto">42</CBadge>
        </CDropdownItem>
        <CDropdownItem divider />*/}

        <CDropdownItem onClick={handleLogout}>
          <CIcon name="cil-lock-locked" className="mfe-2" />
            Logout
        </CDropdownItem>
      </CDropdownMenu>
    </CDropdown>
  )
}

export default TheHeaderDropdown
